.span {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.route {
  width: 65%;
  height: 25vh;
  background-color: rgba(0, 0, 0, 0.7);
  margin-top: 50px;
  border-radius: 25px;
  display: flex;
  justify-content: center;
}
.route .dots {
  display: flex;
  justify-content: space-between;
  width: 65%;
  position: relative;
}
.route .dots .line {
  position: absolute;
  width: 90%;
  left: 5%;
  height: 10px;
  background-color: white;
  top: 50px;
  z-index: 0;
}
.route .dots .dot {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: rgb(255, 255, 255);
  margin-top: 30px;
  font-weight: bold;
  font-size: 2vw;
  z-index: 0;
}
.route .dots .selected {
  background-color: #387AFB;
  width: 65px;
  height: 65px;
  color: white;
  font-size: 2.5vw;
  transform: translateY(-4px);
}

.formContainer {
  display: flex;
  position: relative;
  flex-direction: column;
  justify-content: center;
  width: 35%;
  padding: 20px;
  margin-top: -120px;
  margin-bottom: 20px;
  background-color: #387AFB;
  border-radius: 25px;
}
.formContainer form {
  display: flex;
  flex-direction: column;
}
.formContainer form label {
  display: flex;
  flex-direction: column;
}
.formContainer h2 {
  color: white;
  text-align: center;
  font-size: 3vw;
  margin-bottom: 30px;
}
.formContainer label {
  color: white;
  font-size: 1.6vw;
  margin-bottom: 40px;
}
.formContainer input[type=text], .formContainer input[type=number], .formContainer input[type=email], .formContainer input[type=tel], .formContainer select {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  font-family: Poppins;
}
.formContainer .next, .formContainer .submit {
  width: 30%;
  padding: 12px 20px;
  border-radius: 4px;
  align-self: center;
  border: 2px solid transparent;
  font-family: Poppins;
  color: white;
  background-color: #4eca22;
  box-shadow: 0 7px 5px rgba(0, 0, 0, 0.5);
  transition: all 0.2s ease-in;
  font-size: 1vw;
  font-weight: bold;
  margin-bottom: 30px;
}
.formContainer .next:hover, .formContainer .submit:hover {
  border: 2px solid #fff;
  cursor: pointer;
}
.formContainer .backButton {
  position: absolute;
  bottom: 20px;
  left: 20px;
  color: white;
  cursor: pointer;
}

@media screen and (max-width: 956px) {
  .formContainer {
    width: 50%;
  }
  .formContainer h2 {
    font-size: 4vw;
  }
  .formContainer label {
    font-size: 3vw;
    margin-bottom: 20px;
  }
  .formContainer .next, .formContainer .submit {
    width: 40%;
    font-size: 1.5vw;
  }
}
@media screen and (max-width: 650px) {
  .formContainer {
    width: 65%;
  }
  .formContainer h2 {
    font-size: 7vw;
  }
  .formContainer label {
    margin-bottom: 20px;
    font-size: 5vw;
  }
  .formContainer .carLabel {
    font-size: 3vw;
  }
  .formContainer .next, .formContainer .submit {
    width: 40%;
    font-size: 3vw;
  }
  .route {
    width: 90%;
  }
  .route .dots {
    width: 90%;
  }
  .route .dots .dot {
    font-size: 4vw;
  }
}