body, h1, h2, h3, h4, h5, h6, p, div {
  margin: 0;
  padding: 0;
}

body {
  background-color: rgba(0, 0, 0, 0.07);
  max-width: 1920px;
  margin: auto;
  background-image: url("../src/images/testBackground.png");
  background-repeat: no-repeat;
  background-size: 100% 30%;
}

main {
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-top: 12vh;
}

.flexColumn {
  display: flex;
  flex-direction: column;
}

.flexBetween {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

@media screen and (max-width: 650px) {
  body {
    background-size: 100% 26%;
  }
}